import { delegate } from './../tools/utilities/event-tools';
import { getCurrentCart, ON_CART_FINISHED, ON_CART_PENDING, removeFromCart, updateCart } from '@process-creative/slate-theme-tools';
import { Cart } from '../tools/cart/cart';
import { generateLoader } from '../objects/loader';

const SELECTOR_CONTAINER = '[data-cart-lines]';
const SELECTOR_LOADER = '[data-cart-lines-loader]';

// Listen for cart events
let cartPending = false;
let sectionPending = false;
const pendingStateUpdate = () => {
  const conts = document.querySelectorAll<HTMLDivElement>(SELECTOR_CONTAINER);
  const isPending = cartPending || sectionPending;

  const strLoader = generateLoader({
    container: true,
    attributes: SELECTOR_LOADER.replace(/\[|\]/g, ''),
    clazz: 'c-cart-lines__loader'
  });

  // Add Loader
  conts.forEach(container => {
    if(isPending) {
      const elem = document.createElement('div');
      elem.innerHTML = strLoader;
      container.appendChild(elem.children[0]);
      elem.remove();
    } else {
      container.querySelector<HTMLElement>(SELECTOR_LOADER)?.remove();
    }
  })
}

$(document).on(ON_CART_PENDING, () => {
  cartPending = true;
  pendingStateUpdate();
});

$(document).on(ON_CART_FINISHED, async () => {
  cartPending = false;
  sectionPending = true;
  const data = await fetch('/?sections=cart-lines').then(e => e.json());
  sectionPending = false;
  pendingStateUpdate();

  const child = data['cart-lines'] as string;

  // Replace children contents.
  const conts = document.querySelectorAll<HTMLDivElement>(SELECTOR_CONTAINER);
  conts.forEach(container => {
    const dummy = document.createElement('div');
    dummy.innerHTML = child;
    const newContents = dummy.querySelector(SELECTOR_CONTAINER)!;
    container.parentElement!.appendChild(newContents);
    container.remove();
    dummy.remove();
  });
});
